body {
    background-color: rgb(248, 240, 209);
  }
  
  body {
    display: block;
    text-align: center;
    overflow: hidden;
  }
  
  .canvas-container {
    padding: 0;
    margin: auto;
    position: relative;
    width: 80vw;
    height: 90vh;
    border: solid 15px rgb(192, 239, 208);
    border-radius: 40px;
    background-size: 100% 100%;
  }

  #titleImg{
    position: fixed;
    top: 3%;
    left: 10%;
    width: 80vw;
    height: 90vh;
    z-index: -5;
  }
  
  .another-canvas-container {
    padding: 0;
    margin: auto;
    position: relative;
    width:  80vw;
    height: 90vh;
    border: solid 15px rgb(192, 239, 208);
    border-radius: 40px;
    background-size: 100% 100%;
  }

