  .menu-button {
    position: absolute;
    right: 20%;
    top: 65%;
    width: 7vw;
    height: 10vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    list-style: none;
  }
  
  .menu-button li {
    cursor: pointer;
    margin: 0px auto;
    margin-top: 20px;
    width: 12vw;
    padding: 10px;
    color: #313133;
    font-family: "DynaPuff", cursive;
    border-radius: 10px;
    text-transform: uppercase;
    letter-spacing: 1.3px;
    background: linear-gradient(90deg, rgb(238, 239, 162) 10%, rgb(208, 191, 231) 80%);
    border: none;
    border-radius: 100px;
    box-shadow: 12px 12px 24px rgba(39, 136, 126, 0.64);
    transition: all 0.3s ease-in-out 0s;
    z-index: 1001;
  }
  
  .menu-button li:hover, #return-to-menu:hover {
    color: #e4fffd;
    transform: translateY(-6px);
  }
  
  #instructions-text {
    position: fixed;
    right: 13%;
    top: 50%;
    color: #aa60c0;
    margin: 0 auto;
    width: 20vw;
    height: 25vh;
    border-radius: 80px;
  }

  #instructions-text h2{
    position: fixed;
    font-family: "DynaPuff", cursive;
    font-size: 2.7vh;
    width: inherit;
    height: inherit;
    left: 59.5%;
    bottom: 10%;
    text-shadow: 2cm #050519;
  }
  

  #instructions-text img {
    position: fixed;
    right: 1%;
    bottom: 5%;
    height: 55vh;
    width: 60vw;
    object-fit: contain;

  }
  
  #return-to-menu {
    cursor: pointer;
    position: absolute;
    z-index: 1001;
    right: 19%;
    bottom: 4%;
    width: 100px;
    font-family: "DynaPuff", cursive;
    margin: 0px auto;
    margin-top: 20px;
    width: 150px;
    padding: 10px;
    color: rgb(255, 255, 255);
    text-shadow: 1px 1px rgb(84, 79, 79);
    border-radius: 100px;
    background: linear-gradient(0.25turn, rgb(172, 255, 236) 10%, pink 70%);
  }
  
  .social-buttons {
    position: absolute;
    left: 15px;
    bottom: 15px;
    display: flex;
  }
  
  .social-buttons img {
    margin-right: 10px;
    width: 30px;
    height: 30px;
  }
  
  .social-buttons img:hover {
    cursor: pointer;
    transform: translateY(-6px);
  }
  
  .game-settings-buttons {
    display: flex;
    position: absolute;
    top: 15px;
    right: 15px;
  }
  
  .game-settings-buttons img {
    margin-bottom: 10px;
    width: 50px;
    height: 50px;
  }
  
  .game-settings-buttons img:hover {
    cursor: pointer;
    transform: translateY(-6px);
  }
  
  #game-canvas {
    border-radius: 25px;
  }
  
